<template>
  <div class="my-pages">
    <the-navbar />
    <main class="home main mt-5 pt-5">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <h1 class="h3 text-center mb-5">Mis Páginas</h1>
          </div>
          <div
            class="col-12 col-sm-4 col-lg-3"
            v-for="(item, index) in getMyPages"
            :key="index"
          >
            <Site :image="item.image" :name="item.name" :slug="item.slug" />
          </div>
        </div>
      </div>
    </main>
    <div class="container">
      <div class="row">
        <div class="col-12">
          <the-footer />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { fb } from "@/firebase";

import Site from "@/components/Site";
import TheFooter from "@/components/TheFooter";
import TheNavbar from "@/components/TheNavbar";

export default {
  name: "MyPages",
  components: {
    Site,
    TheFooter,
    TheNavbar
  },
  async created() {
    const user = await fb.auth().currentUser;
    this.fetchMyPages(user.uid);
  },
  methods: {
    ...mapActions(["fetchMyPages"])
  },
  computed: {
    ...mapGetters(["getMyPages"])
  }
};
</script>

